import React, { Component } from 'react';
import './feedback.module.css';

class Fedback extends Component {
  //   static propTypes = {
  //     title: T.string,
  //   };

  static defaultProps = {
    title: 'Please leave feedback',
  };

  state = {
    good: 0,
    neutral: 0,
    bad: 0,
  };

  handleIncrementGood = () => {
    this.setState(prevState => {
      return { good: prevState.good + 1 };
    });
  };
  handleIncrementNeutral = () => {
    this.setState(prevState => {
      return { neutral: prevState.neutral + 1 };
    });
  };
  handleIncrementBad = () => {
    this.setState(prevState => {
      return { bad: prevState.bad + 1 };
    });
  };

  render() {
    return (
      <div>
        <h2>Please leave feedback</h2>
        <div>
          <button type="button" onClick={this.handleIncrementGood}>
            Good
          </button>
          <button type="button" onClick={this.handleIncrementNeutral}>
            Neutral
          </button>
          <button type="button" onClick={this.handleIncrementBad}>
            Bad
          </button>
        </div>
        <h2>Statistics</h2>
        {/* Object.values(this.state).reduce((a, b) => a + b, 0) === 0 ? <Notification message="No feedback given">.: */}
        <ul>
          <li>Good:{this.state.good}</li>
          <li>Neutral:{this.state.neutral}</li>
          <li>Bad:{this.state.bad}</li>
          <li>Total: {Object.values(this.state).reduce((a, b) => a + b, 0)}</li>
          <li>
            Positive feedback:{' '}
            {Math.round(
              (this.state.good * 100) /
                Object.values(this.state).reduce((a, b) => a + b, 0),
            )}
            %
          </li>
        </ul>
      </div>
    );
  }
}

export default Fedback;
